import uniPopup from "../../components/uni-popup/uni-popup.vue"
import uniCountdown from "@/components/uni-countdown/uni-countdown.vue"
export default {
  components: {uniCountdown,uniPopup},
	data(){
		return{
			title: 'map',
			latitude: 0,
			longitude: 0,
			covers: [ {
				id:2,
				latitude: 31.174439,
				longitude: 121.556339,
				iconPath: '../../static/dian.png'
			}],
			mapObj:{},
			Mapheight:0,
			minute:1,
			sec:4,
			show: false,
			type:'center',
			
		}
	},
	methods:{
		aaaaaaa(e){
			if(e.type === 'end'){
				console.log('松开屏幕')
				var a = uni.createMapContext('mapId',this)
				//获取当前地图中心位置
				// a.getCenterLocation({
				// 	success:(res)=>{
				// 		console.log('我获取到中心了')
				// 	},
				// 	fail:()=>{
				// 		console.log('失败')
				// 	}
				// })
				//获取当前视野范围
					a.getRegion({
						success:(res)=>{
							console.log('我获取到视野范围了')
						},
						fail:()=>{
							console.log('失败')
						}
					})
			}
		},
		clickmarkertap(e){
			console.log('我点击了icon')
			console.log(e)
			
			uni.navigateTo({
			    url: `../netdetails/netdetails?id=${e.markerId}`
			});
		},
		//地图更新完成之后
		asdasdasd(e){
			console.log('渲染完成')
				
		},
		
		//点击回到当前定位位置
		clickgoaddress(){
			uni.getLocation({
			    type: 'gcj02', //返回可以用于uni.openLocation的经纬度
			    success:  (res)=> {
					var a = uni.createMapContext('mapId',this)
					a.moveToLocation({
						latitude : res.latitude,
						longitude : res.longitude
					})
					this.latitude =  res.latitude,
					this.longitude =  res.longitude
			    }
			});
			
		},
		
		//进入我的界面
		clickgohome(){
			uni.navigateTo({
			    url: '../usermiddle/usermiddle'
			});
		},
		
		
		//弹出客服框
		showservice(){
			
		},
		
		//倒计时结束回调
		timeup(){
			console.log("结束了")
		},
		
		//打开  已被预约提醒  + 暂未认证提醒
		quedingnle(){
			this.show = true
			
		},
		//关闭  已被预约提醒  + 暂未认证提醒
		cancel(){
			this.show=false
			
			uni.showToast({
			    title: '取消成功',
				icon :'none',
			    duration: 1500
			});
		},
		
		//点击进入扫一扫
		gosao(){
			uni.navigateTo({
			    url: '../home/home'
			});
		},
		
		
		//进入网点搜索
		searchaddress(){
			uni.navigateTo({
			    url: '../searchaddress/searchaddress'
			});
		}
	
	},
	mounted() {
		uni.getLocation({
		    type: 'gcj02', //返回可以用于uni.openLocation的经纬度
		    success:  (res)=> {
				this.$nextTick(()=>{
					this.latitude = res.latitude
					this.longitude = res.longitude
				})
		    }
		});
		
		 
		//创建节点选择器
		const query = wx.createSelectorQuery()
		    query.select('#test').boundingClientRect()
		    query.selectViewport().scrollOffset()
		    query.exec( (res) =>{
				this.Mapheight = res[0].height
		    })
	}
}
